import React from "react";
import styles from "../../app.module.css";

export const Ridic = () => {
  const [count, newCount] = React.useState("0");

  function decreaseCount() {
    newCount(count - 1);
  }
  function incrementCounter() {
    newCount(count + 1);
  }

  function resetCounter() {
    newCount(0);
  }

  const backgroundColor = (background) => {
    if (count > 0) {
      background = "green";
    }
    if (count < 0) {
      background = "red";
    }
    if (count === 0) {
      background = "#222";
    }
  };

  return (
    <>
      <h2 style={{ backgroundColor }}>
        <span className={styles.color}>{count}</span>
      </h2>
      <button
        className={(styles.btn, styles.btn_hero)}
        onClick={incrementCounter}
      >
        +
      </button>
      <button className={(styles.btn, styles.btn_hero)} onClick={resetCounter}>
        reset
      </button>
      <button className={(styles.btn, styles.btn_hero)} onClick={decreaseCount}>
        -
      </button>
    </>
  );
};
