const SET_USERS_DATA = "SET_USERS_DATA";

let initialState = {
  userId: null,
  email: null,
  password: null,
  isAuth: false
};

const AuthReduser = (state = initialState, action) => {
  switch (action.type) {
    case SET_USERS_DATA:
      return {
        ...state,
        ...action.data,
        isAuth: true
      };
    default:
      return state;
  }
};

export const setUserData = (userId, email, password) => ({
  type: SET_USERS_DATA,
  data: { userId, email, password }
});

export default AuthReduser;
