import React from "react";

import * as axios from "axios";
import { connect } from "react-redux";
import { setUserData } from "../../../../redux/auth-reduser";
import { withRouter } from "react-router-dom";
import { Login } from "./Login";

class LoginContainer extends React.Component {
  componentDidMount() {
    axios
      .get(`https://reqres.in/api/api/login/`, { whithCredentials: true })
      .then((response) => {
        this.props.setUserData(response.data.data);
      });
  }

  render() {
    return <Login {...this.props} data={this.props.data} />;
  }
}

let mapStateToProps = (state) => ({
  profile: state.auth.data
});

let ProfilePageUrlData = withRouter(LoginContainer);

export default connect(mapStateToProps, { setUserData })(ProfilePageUrlData);
