import React from "react";
import "antd/dist/antd.css";
import styles from "./users.module.css";
import "antd/dist/antd.css";
import { Button } from "antd";
import { NavLink } from "react-router-dom";

const NewMembers = (props) => {
  let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }
  return (
    <>
      <div>
        {pages.map((p) => {
          return (
            <Button
              style={{ marginBottom: "15px" }}
              className={props.currentPage === p && styles.selectid_Page}
              onClick={(e) => props.onPageChanged(p)}
            >
              {p}
            </Button>
          );
        })}
      </div>
      {props.users.map((u) => (
        <div className={styles.posts_content} key={u.id}>
          <div className={styles.avatar}>
            <NavLink to={"/profile/" + u.id}>
              <div>
                <img
                  src={
                    u.avatar != null
                      ? u.avatar
                      : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRYwAAVsLd7PDyf9XTI_XHd-F-w_lfF8-2r9Q&usqp=CAU"
                  }
                  alt="logo1"
                />
              </div>
              <h3>{u.first_name + " " + u.last_name}</h3>
            </NavLink>
            <>
              {u.followed ? (
                <Button
                  type="primary"
                  onClick={() => {
                    props.unfollow(u.id);
                  }}
                >
                  Unfollow
                </Button>
              ) : (
                <Button
                  onClick={() => {
                    props.follow(u.id);
                  }}
                >
                  Follow
                </Button>
              )}
            </>
            <div>{u.email}</div>
            <div className={styles.like}></div>
          </div>
        </div>
      ))}
    </>
  );
};
export default NewMembers;

