import React from "react";
import * as axios from "axios";
import { connect } from "react-redux";
import {
  follow,
  unfollow,
  setUsers,
  setCurrentPage,
  setTotalUsersCount,
  toggleIsFetching
} from "../../../redux/user-reduser";
import NewMembers from "./NewMembers";
import { Preloader } from "../../Prelouder/Preloader";

class NewMembersContainer extends React.Component {
  componentDidMount() {
    this.props.toggleIsFetching(true);
    axios
      .get(
        `https://reqres.in/api/users?page=${this.props.currentPage}&per_page=${this.props.pageSize}`
      )
      .then((response) => {
        this.props.toggleIsFetching(false);
        this.props.setUsers(response.data.data);
        this.props.setTotalUsersCount(response.data.total);
      });
  }
  onPageChanged = (pageNumber) => {
    this.props.setCurrentPage(pageNumber);
    this.props.toggleIsFetching(true);
    axios
      .get(
        `https://reqres.in/api/users?page=${pageNumber}&per_page=${this.props.pageSize}`
      )
      .then((response) => {
        this.props.toggleIsFetching(false);
        this.props.setUsers(response.data.data);
      });
  };
  render() {
    return (
      <>
        {this.props.isFetching ? <Preloader /> : null}
        <NewMembers
          totalUsersCount={this.props.totalUsersCount}
          pageSize={this.props.pageSize}
          currentPage={this.props.currentPage}
          onPageChanged={this.onPageChanged}
          users={this.props.users}
          follow={this.props.follow}
          unfollow={this.props.unfollow}
        />
      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    users: state.NewMembersPage.users,
    pageSize: state.NewMembersPage.pageSize,
    totalUsersCount: state.NewMembersPage.totalUsersCount,
    currentPage: state.NewMembersPage.currentPage,
    isFetching: state.NewMembersPage.isFetching
  };
};
export default connect(mapStateToProps, {
  follow,
  unfollow,
  setUsers,
  setCurrentPage,
  setTotalUsersCount,
  toggleIsFetching
})(NewMembersContainer);

