import React from "react";
import { MemberDesk } from "./MemberDesk/MemberDesk";
import { MessageDesk } from "./MessageDesk/MessageDesk";
import styles from "./messagepage.module.css";

export const MissageArea = (props) => {
  return (
    <div class={styles.wrap}>
      <div class={styles.floatleft}>
        <MemberDesk />
      </div>
      <div class={styles.floatright}>
        <MessageDesk />
      </div>
    </div>
  );
};
