import React from "react";
import styles from "../../app.module.css";

export const Hex = () => {
  const [color, setColor] = React.useState("");
  const LIST_COLOR = ["CadetBlue", "blue", "green", "black", "Brown"];

  function randomColor() {
    const randomIndex = Math.trunc(Math.random() * LIST_COLOR.length);
    const colorPick = LIST_COLOR[randomIndex];
    return colorPick;
  }
  function handleBoxClick() {
    const colorClick = randomColor();
    setColor(colorClick);
    return colorClick;
  }
  function handleAddColor() {
    LIST_COLOR.push("pink");
  }
  /*

*/
  return (
    <>
      <h2 style={{ backgroundColor: color }}>
        background color : <span className="color">{handleAddColor}</span>
      </h2>
      <button
        className={(styles.btn, styles.btn_hero)}
        onClick={handleBoxClick}
      >
        Toggle Color
      </button>
      <button className={(styles.btn, styles.btn_hero)} id="btn">
        click me
      </button>
    </>
  );
};
