import React from "react";
import { RightMenu } from "./Menu/Menu";
import { TextBody } from "./TextBody/TextBody";
import SplitPane from "react-split-pane";
import styles from "./main.module.css";

export const Main = (props) => {
  return (
    <div>
      <SplitPane
        clssName={styles.main_lending}
        split="vertical"
        defaultSize={85}
      >
        <div className="left">
          <RightMenu />
        </div>
        <div className="right">
          <TextBody />
        </div>
      </SplitPane>
    </div>
  );
};
