import React from "react";
import Simple from "../../Simple/simple";
import { Hex } from "../../Counter/hex";
import { Switch, Route } from "react-router-dom";
import { Ridic } from "../../Ridic/ridic";
import styles from "../../../app.module.css";
import { MissageArea } from "./Messages/MissageArea";
import { Option5Axios } from "./Option5Axios/Option5Axios";
import NewMembersContainer from "../NewMembers/NewMembersContainer";
import ProfileContainer from "./ProfilePage/ProfileContainer";
import LoginContiner from "./Login/LoginContiner";

export const TextBody = (props) => {
  const CompMissageArea = () => <MissageArea />;
  return (
    <main>
      <div className={styles.container}>
        <Switch>
          <Route exact path="/" render={() => <Simple />} />
          <Route path="/hex" component={Hex} />
          <Route path="/ridic" component={Ridic} />
          <Route path="/profile/:userId?" component={ProfileContainer} />
          <Route path="/messages" component={CompMissageArea} />
          <Route path="/option5" component={Option5Axios} />
          <Route path="/newmembers" component={NewMembersContainer} />
          <Route path="/login" component={LoginContiner} />
        </Switch>
      </div>
    </main>
  );
};
