import React, { useState, useEffect } from "react";
import axios from "axios";
import styles from "./MessageDesk.module.css";

export const MemberDesk = (props) => {
  const [articles, setArticles] = useState({ results: [] });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  useEffect(() => {
    const getArticles = async () => {
      setLoading(true);
      setError(false);
      try {
        const resp = await axios("https://randomuser.me/api/?results=5");
        setArticles(resp.data);
      } catch (error) {
        setError(true);
      }
      setLoading(false);
    };
    getArticles();
  }, []);
  return (
    <div className={styles.avatar}>
      <h2>User's Messange</h2>
      {error ? (
        <p>Error, something went wrong...</p>
      ) : loading ? (
        <p>Loading...</p>
      ) : (
        articles.results.map((article) => (
          <div key={article.login.uuid}>
            <img src={article.picture.medium} alt={"logo2"} />
            <h3>{article.name.first + " " + article.name.last}</h3>
            <h4>{article.location.city}</h4>
            <p>{article.email}</p>
          </div>
        ))
      )}
    </div>
  );
};
