import React from "react";
import ProfilePage from "./ProfilePage";
import * as axios from "axios";
import { connect } from "react-redux";
import { setUserProfile } from "../../../../redux/profile-reducer";
import { withRouter } from "react-router-dom";

class ProfileContainer extends React.Component {
  componentDidMount() {
    let userId = this.props.match.params.userId;
    if (!userId) {
      userId = 4;
    }
    axios.get(`https://reqres.in/api/users/` + userId).then((response) => {
      this.props.setUserProfile(response.data.data);
    });
  }

  render() {
    return <ProfilePage {...this.props} profile={this.props.profile} />;
  }
}

let mapStateToProps = (state) => ({
  profile: state.profilePage.profile
});

let ProfilePageUrlData = withRouter(ProfileContainer);

export default connect(mapStateToProps, { setUserProfile })(ProfilePageUrlData);
